@import "variables";
.Route{
  display: flex;
  justify-content: center;
  &__container{
    display: flex;
    flex-direction: column;
    align-items: center;
    &-title{
      background: $gradient;
      -webkit-text-fill-color: transparent;
      -webkit-background-clip: text !important;
      font-size: $f-middle;
      font-weight: $fw-huge;
      width: fit-content;
      margin-bottom: 20px;
    }
    img{
      width: 50%;
      margin-bottom: 80px;
    }
  }
}
@media (max-width: 600px) {
  .Route{
    &__container{
      &-title{
        font-weight: $fw-middle;
      }
    }
  }
}